.photos {
  $indent: 20px;
  display: flex;
  justify-content: space-between;

  &__img {
    height: 261px;
    width: calc(50% - #{$indent/2});
    object-fit: fill;
    object-position: center;
  }

  &_first-more {
    .photos__img:first-child {
      width: calc(66% - #{$indent/2});
    }

    .photos__img:last-child {
      width: calc(34% - #{$indent/2});
    }
  }

  &_second-more {
    .photos__img:first-child {
      width: calc(34% - #{$indent/2});
    }

    .photos__img:last-child {
      width: calc(66% - #{$indent/2});
    }
  }
}
